import React from 'react'

const OpenSourceProjects = ( ) => {

    return (
    <div className='content-wrapper'>
        <div className="ops-container">
            <div className="ops-card-container">
                <div className="ops-card">
                
                </div>
                <h3 className='content-text-subheading'>
                    A portfolio starter for Gatsby integrated with Contentful CMS. This website is based on the starter.
                </h3>
            </div>
        </div>
        <style jsx>{`
            .ops-container{
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                overflow-x: auto;
            }
            .ops-card-container{
                display: flex;
                align-items: flex-start;
                flex-direction: column;
                max-width:424px;
            }
            .ops-card{
                min-width:100px;
                min-height:100px;
                border-radius:14px;
                background: #fff;
            }
        `}</style>
    </div>
    )
}

export default OpenSourceProjects